<template>
<el-card class="card_back">
    <template #header>
        <div class="buttons" >
        <el-select v-model="rolestate">
            <el-option label="未审核" value="0"></el-option>
            <el-option label="已审核" value="1"></el-option>
        </el-select>
    </div>
    </template>
    <table-page :url="tableinit.url" :datatitle="tableinit.datatitle" :parems="tableinit.parems" :opreate="tableinit.opreate"></table-page>
</el-card>
</template>

<script>
export default {
    computed:{
        examineShow(){
           return this.utils.hasLoginComponents('sys:userMan:roleOpera') && this.rolestate == 0||this.rolestate==null
        },
        retrialShow(){
           return this.utils.hasLoginComponents('sys:userMan:roleOpera') && this.rolestate == 1
        },
    },
    data() {
        let vm = this;
        let has = vm.utils.hasLoginComponents;
        return {
            rolestate: "0",
            tableinit: {
                url: "/selectUserPaging.action",
                parems: {
                    state:0
                },
                datatitle: [{
                    prop: "userid",
                    width: '100',
                    label: '用户编号',
                    fixed:true
                }, {
                    prop: "username",
                    width: '100',
                    label: '用户名称'
                }, {
                    prop: "tel",
                    width: '150',
                    label: '电话'
                }, {
                    prop: "email",
                    width: '200',
                    label: '邮箱'
                }, {
                    prop: "name",
                    width: '100',
                    label: '姓名'
                }, {
                    prop: "state",
                    width: '100',
                    label: '账号状态',
                    component:{
                        render:vm.stateRender
                    },
                }, {
                    prop: "regtime",
                    width: '200',
                    label: '注册时间',
                    formatter: vm.dateFormatter,
                }, {
                    prop: "type_name",
                    width: '100',
                    label: '所属平台',
                }, {
                    prop: "rolename",
                    width: '100',
                    label: '角色名称',
                }, {
                    prop: "role_state",
                    width: '100',
                    label: '角色状态',
                    component:{
                        render:vm.roleStateRender
                    },
                }],
                opreate: {
                    label: "操作",
                    minwidth: "200",
                    btn: [{
                        type: "primary",
                        size: 'mini',
                        icon: "el-icon-check",
                        text: "审核",
                        btnfun: vm.examine,
                        circle:true,
                        show: vm.examineShow||true
                    }, {
                        type: "warning",
                        size: 'mini',
                        icon: "el-icon-close",
                        text: "冻结",
                        btnfun: vm.retrial,
                        circle:true,
                        show: vm.retrialShow
                    }, {
                        type: "danger",
                        size: 'mini',
                        icon: "el-icon-delete",
                        text: "删除",
                        btnfun: vm.delete,
                        circle:true,
                        show: has('sys:userMan:userOpera')
                    }, ]
                }
            },
        }
    },
    watch: {
        rolestate(news) {
            let vm = this;
            this.tableinit.parems.state = news;
            this.tableinit.opreate.btn[0].show =this.examineShow;
            this.tableinit.opreate.btn[1].show = this.retrialShow;
        }
    },
    components: {
        tablePage: () => import('@/components/tablePage.vue'),
    },
    methods: {
        stateRender(h,row, column, index) {
            let vm = this;
            const map = new Map([
                [0, () => {
                    return <el-tag type="warning">注销</el-tag>
                }],
                [1, () => {
                    return <el-tag type="success">正常</el-tag>
                }],
                [2, () => {
                    return <el-tag type="danger">冻结</el-tag>
                }]
            ])
            return row.state != null ? map.get(Number(row.state))() : null;
        },
        roleStateRender(h,row, column, index) {
            let vm = this;
            const map = new Map([
                [0, () => {
                    return <el-tag type="warning">未审核</el-tag>
                }],
                [1, () => {
                    return <el-tag type="success">已审核</el-tag>
                }],
                [2, () => {
                    return <el-tag type="danger">冻结</el-tag>
                }]
            ])
            return row.role_state != null ? map.get(Number(row.role_state))() : null;
        },
        dateFormatter(row, column, cellValue, index) {
            let vm = this;
            return vm.utils.date.formatter(cellValue)
        },
        examine(row){
            this.$api.opreateRequset('/examineUser.action', vm.utils.data.formatter(row),'审核');
        },
        retrial(row){
            this.$api.opreateRequset('/frozenUser.action', vm.utils.data.formatter(row),'冻结');
        },
        delete(row) {
            let vm = this;
            let datas = {
                id: row.userid,
            };
            vm.$api.deleteRequset('/deleteUserData.action', vm.utils.data.formatter(datas));
        }
    }
}
</script>

